import{maxNum as o}from"./colorParse";import r from"./hsvToRgb";export default function(t){let[a,f,m,l=1]=t,h=r([a,f,m,l]),i=h[0]/255,n=h[1]/255,e=h[2]/255,M=Math.min(1-i,1-n,1-e),c=[Math.floor(100*(1===M?0:(1-i-M)/(1-M))),Math.floor(100*(1===M?0:(1-n-M)/(1-M))),Math.floor(100*(1===M?0:(1-e-M)/(1-M))),Math.floor(100*M)];return c.toString=()=>`cmyk(${c.join("%, ")})`,c.max=o.cmyk,c}