import{maxNum as o}from"./colorParse";export default function(r){let[t,a,l,f=1]=r,e=t/360*6,i=a/100,n=l/100,g=Math.floor(e),h=e-g,M=n*(1-i),b=n*(1-h*i),c=n*(1-(1-h)*i),m=g%6,u=[Math.floor(255*[n,b,M,M,c,n][m]),Math.floor(255*[c,n,n,b,M,M][m]),Math.floor(255*[M,M,c,n,n,b][m]),f];return u.toString=()=>f<1?`rgba(${u.join(",")})`:`rgb(${u.slice(0,3).join(",")})`,u.max=o.rgba,u}