import{css as o}from"@moneko/css";export const style=o`
  :host {
    display: inline-flex;
  }

  .tag {
    display: flex;
    align-items: center;
    overflow: hidden;
    border-radius: calc(var(--border-radius) / 1.5);
    padding: 0 8px;
    font-size: var(--font-size-sm);
    color: var(--text-color);
    background-color: var(--component-bg);
    gap: 5px;
    line-height: 20px;

    a {
      text-decoration: unset;
      color: inherit;
    }
  }

  .icon {
    font-size: var(--font-size-sm);
    line-height: var(--font-size-sm);
  }

  .bordered {
    border: 1px solid var(--border-color);
  }

  .close {
    font-size: var(--font-size-sm);
    cursor: pointer;
    opacity: 0.5;
    transition: var(--transition-duration) var(--transition-timing-function) opacity;
    user-select: none;

    &:hover {
      opacity: 1;
    }
  }

  ${["primary","success","error","warning","tag-custom"].map(o=>`.${o} {--text-color: var(--${o}-color);--border-color: var(--${o}-secondary-bg);--component-bg: var(--${o}-details-bg);}`).join("")}

  .disabled {
    --text-color: var(--disable-color);
    --border-color: var(--disable-border);
    --component-bg: var(--disable-bg);
  }
`;